import Title from "./Title";
// import Subject from "./Subject";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
function Section3(){
	let data=useSelector(state => state.title);
let navigate=useNavigate();

	return(
		<section id="section3">
		<div className="inner">
			<Title  propsValue={data[3]}/>
			<div className="content">
				<a href="">
					<span data-aos="fade" data-aos-delay="700">Link</span>
					{/* <i data-aos="fade" data-aos-delay="850">view more</i> */}
					<button onClick={() => navigate("/subject")} data-aos="fade" data-aos-delay="850">view more</button>
				</a>
			</div>
		</div>
	</section>
	);
}
export default Section3;