function Imformation(props){
	let data=props.propsValue;
	return(
		<div className="information" data-aos="fade" data-aos-delay="1200">
		<h2 >{data.h2}</h2>
		<p>
			<span>{data.span.phone} {data.p.phone}</span>
			<span>{data.span.email} {data.p.email}</span>
			<span>{data.span.adress} {data.p.adress}</span>
		</p>
	</div>
	);
}
export default Imformation;