function MainSkill(props){
	let data=props.propsValue;
	return(
		<div className="box">
		<ul>
			{
				data.map((d,i) => <li key={i} data-aos="fade-up" data-aos-delay={d.delay}>
				<a href="">
					<h3><span className="em">{d.number}</span><span>{d.span}</span></h3>
					<p>{d.p}</p>
				</a>
			</li>)
			}
		</ul>
	</div>
	);
}
export default MainSkill;