export default configureStore({
	reducer: {
		navigaton: navigaton.reducer,
		keytext: keytext.reducer,
		subject: subject.reducer,
		information: information.reducer,
		aspiration: aspiration.reducer,
		title: title.reducer,
		skillswiper: skillswiper.reducer,
		footer: footer.reducer,
		subcontent: subcontent.reducer,
		subslider: subslider.reducer,
		subfooter: subfooter.reducer,
	}
});