import Title from "./Title";
import Imformation from "./Imformation";
import Aspiration from "./Aspiration";
import { useSelector } from 'react-redux';
function Section1(){
	let data1=useSelector(state => state.information);
	let data2=useSelector(state => state.aspiration);
	let data3=useSelector(state => state.title);

	return(
		<section id="section1">
		<div className="inner">
			<Title propsValue={data3[1]}/>
			<div className="content">
				<div className="photo" data-aos="fade" data-aos-delay="1000"></div>
				<Imformation propsValue={data1}/>
				<Aspiration propsValue={data2}/>
			</div>
		</div>
	</section>
	);
}
export default Section1;