import Keytext from "./Keytext";
import MainSkill from "./MainSkill";
import { useSelector } from 'react-redux';
function Main(){
	let data1=useSelector(state => state.keytext);
	let data2=useSelector(state => state.subject);

	return(
		<div className="main">
			<video id="main_video" poster="./assets/video/videoframe.png"></video>
			<div className="inner">
				<Keytext propsValue={data1}/>
				<MainSkill propsValue={data2}/>
			</div>
		</div>
	);
}
export default Main;