import Sub1list from "./Sub1list";
import { useSelector } from "react-redux";
function Sub1(){
	let data=useSelector(state => state.subcontent)
return(
	<section id="sub1">
		<div className="inner">
			<div className="title" data-aos="fade" data-aos-delay="500" >Project</div>
			<div className="content">
				<ul>
					{
						data.map((d,i) => <Sub1list key={i} propsValue={d}/>)
					}
				</ul>
			</div>
		</div>
	</section>
);
}
export default Sub1