import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import { useSelector } from 'react-redux';
function Sub1(){
	let data=useSelector(state => state.subslider);
	console.log(data);
	return(
		<section id="sub2">
			<div className="inner">
			<div className="title" data-aos="fade" data-aos-delay="500" >Open Source</div>
			<Swiper
			slidesPerView={1}
			spaceBetween={10}
			breakpoints={{
				640: {
					slidesPerView: 1,
					spaceBetween: 20
				},
				768: {
					slidesPerView: 2,
					spaceBetween: 20
				},
				1024: {
					slidesPerView: 4,
					spaceBetween: 20
				}
			}}
			onSwiper={swiper => {
				// console.log(swiper);
				// console.log("swiper init event");
				// console.log(swiper.slides.length);
			}}
			onSlideChange={swiper => {
				// console.log("slide change event");
				// console.log(swiper.activeIndex);
			}}
			className="mySwiper"
		>
			{
				data.map((d,i) => 	
					<SwiperSlide key={i}>
					<div className="image"><img src={"./assets/images/"+d.img} alt="notification" /></div>
				<p className="name">{d.name}</p>
				<div className="desc">
					<h4>{d.desc}</h4>
				</div>
					</SwiperSlide>		
				)
			}

		</Swiper>
		</div>
		</section>
	)

}
export default Sub1