function ProjectList(props){
	let data=props.propsValue;

	return(
		<li>
			<div className="desc" data-aos="fade" data-aos-delay="500">
				<div className="number">{data.num}</div>
				<div className="subject">{data.category}</div>
				<div className="icon">
					<ul>
						{data.path.map((d,i) => <li key={i}><img src={"./assets/images/"+d.img} alt={d.alt} /></li>)}
					</ul>
				</div>
				<div className="text"><span>{data.desc[0]}</span><span>{data.desc[1]}</span></div>
			</div>
		</li>
	);
}
export default ProjectList;