import Title from "./Title";
import Footer from "./Footer";
import { useSelector } from "react-redux";
function Section4(){
	let data1=useSelector(state => state.title);
	let data2=useSelector(state => state.footer);
	
	return(
		<section id="section4">
		<div className="inner">
			<Title  propsValue={data1[4]}/>
			<Footer propsValue={data2}/>
		</div>
	</section>
	);
}
export default Section4;