import Title from "./Title";
import ProjectList from "./ProjectList";
import { useSelector } from 'react-redux';
function Section2(){
	let data1=useSelector(state => state.title);
	let data2=useSelector(state => state.skillswiper);

	return(
		<section id="section2">
		<div className="inner">
			<Title  propsValue={data1[2]}/>
			<div className="content">
				<div className="content_inner" data-aos="fade" data-aos-delay="500">
					<ul>
						{
							data2.map((d, i) => <ProjectList key={i} propsValue={d} />)
						}
					</ul>
				</div>
			</div>
		</div>
	</section>
	);
}
export default Section2;