import { useSelector } from "react-redux";
function SubFooter(){
	let data=useSelector(state => state.subfooter);
	return(
		<footer>
		<div className="footer_inner">
			<div className="track" data-aos="fade" data-aos-delay="700">
				{data.track}
			</div>
			<div className="more" data-aos="fade" data-aos-delay="700">{data.more}</div>				
		</div>
	</footer>
	);
}
export default SubFooter