function Sub1list(props){
	let data=props.propsValue;

	return(
		<li>
			<p>{data.tit}</p>
			<div className="desc" data-aos="fade" data-aos-delay="700">
				<div className="tit">
					<ul>
						{data.sub.map((d,i) => <li key={i}>{d}</li>)}
					</ul>
				</div>
				<div className="menual">
					<div className="sub">
						<a href=""><img src={"./assets/images/"+data.img[0]} alt={data.alt[0]}/></a>
						<a href=""><img src={"./assets/images/"+data.img[1]} alt={data.alt[1]}/></a>
					</div>
					<h3>{data.h3}</h3>
				</div>
			</div>
		</li>
	)
}
export default Sub1list;