import { useEffect } from "react";
import Navigation from "./Navigation";
import ScrollTrigger from '@terwanerik/scrolltrigger';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import gsap from 'gsap';
import AOS from 'aos';
import 'aos/dist/aos.css';

function Header(){
	useEffect(() =>{
		gsap.registerPlugin(ScrollToPlugin);
		const trigger=new ScrollTrigger({
			trigger: {
				once: true,
				toggle: {
					class: {
						in: "active",
						out: "inactive"
					}
				},
				offset: {
					viewport: {
						y: 0.25
					}
				}
			},
		});
		trigger.add("#start, section");
	
		AOS.init({
			easing: "ease-in-out-sine",
			once: true
		});
		let n=0; 
		let t=0; 
		let topPos=0;
		
		let navUl=document.querySelector("#nav > ul");
		let navLi=document.querySelectorAll("#nav > ul > li");
		let header=document.querySelector("#start");
		let menuArea=header.firstElementChild;
	
		let mainVideo=document.getElementById("main_video");
		mainVideo.muted=true;
		mainVideo.setAttribute("src", "video/particle.mp4");
	
		mainVideo.addEventListener("loadeddata", function(){
			mainVideo.play();
		});
	
		mainVideo.addEventListener("ended", function(){
			mainVideo.play();
		});
	
		let section=document.querySelectorAll("section");
	
		let gnb=document.getElementById("pc");
		let gnbList=gnb.firstElementChild.children;
		gnbList[n].firstElementChild.classList.add("on");
	
		let mobileGnb=document.getElementById("mobile");
		let mobileGnbList=mobileGnb.firstElementChild.children;
	
		let body=document.querySelector("body");
		let mobile=document.querySelector("#mobile");
		let mobileUl=mobile.firstElementChild;
		let mobileLi=mobileUl.firstElementChild;
		let tab=document.querySelector("#tab");
	
		let dim=document.getElementById("dim")
	
		for(let i=0; i<navLi.length; i++){
			navLi[i].addEventListener("mouseenter",function(){
				navUl.classList.add("on");
			});
			navLi[i].addEventListener("mouseleave",function(){
				navUl.classList.remove("on");
			});
		}
	
		for(let i=0; i<menuArea.children.length; i++){
			if(menuArea.children[i].className === "mobile"){
				mobile=menuArea.children[i];
			}
			else if(menuArea.children[i].className === "tab"){
				tab=menuArea.children[i];
			}
			else if(menuArea.children[i].className == "dim"){
				dim=menuArea.children[i];
			}
		}

		let pageList=[];
		pageList[0]=header;
		for(let i=0; i<section.length; i++){
			pageList.push(section[i]);
		}

		window.addEventListener("scroll", function(){
			t=window.scrollY;
			if(t < pageList[1].offsetTop){
				n=0;
			}
			else if(t < pageList[2].offsetTop){
				n=1;
			}
			else if(t < pageList[3].offsetTop){
				n=2;
			}
			else if(t < pageList[4].offsetTop){
				n=3;
				if(window.innerHeight+t == document.body.scrollHeight){
					n=4;
				}
			}
			else{
				n=4;
			}
			if(n === 0){
				menuArea.classList.remove("active");
			}
			else{
				menuArea.classList.add("active");
			}
			for(let i=0; i<gnbList.length; i++){
				if(i == n){
					gnbList[i].firstElementChild.classList.add("on");
					mobileGnbList[i].classList.add("active");
				}
				else{
					gnbList[i].firstElementChild.classList.remove("on");
					mobileGnbList[i].classList.remove("active");
				}
			}
		});

			tab.addEventListener("click",function(e){
				e.preventDefault();
				mobile.classList.toggle("active");
				tab.classList.toggle("active");
				dim.classList.toggle("active");				
			});

			dim.addEventListener("click", function(){
				mobile.classList.remove("active");
				tab.classList.remove("active");
				dim.classList.remove("active");
			});

		mobileUl.addEventListener("click",function(e){
			e.preventDefault();
			mobileLi.classList.add("active");
		});
	
		for(let i=0; i<gnbList.length; i++){
			gnbList[i].addEventListener("click", function(e){
				e.preventDefault();
	
				n=i;
				topPos=pageList[n].offsetTop;
	
				gsap.to(window, {scrollTo: topPos, duration: 0.4});
			});
	
			mobileGnbList[i].addEventListener("click", function(e){
				e.preventDefault();
	
				n=i;
				topPos=pageList[n].offsetTop;
	
				mobile.classList.remove("active");
				tab.classList.remove("active");
				dim.classList.remove("active");
	
				gsap.to(window, {scrollTo: topPos, duration: 0.4, delay: 0.4});
			});
		}
	});

	return(
		<header>
			<div className="header_inner">
				<div className="logo"><a href="">LDH</a></div>
				<Navigation propsValue="pc" />
				<Navigation propsValue="mobile" />
			<a href="" id="tab"></a>
			<div id="dim"></div>	
			</div>
		</header>
	);
}
export default Header;