function Keytext(props){
	let data=props.propsValue;
	return(
		<div className="keytext" >
		<h2 data-aos="fade" data-aos-delay={data.h2.delay}>{data.h2.text}</h2>
		<div className="moto" >
			<p data-aos="fade" data-aos-delay={data.p.delay}>{data.p.text}</p>
			<span data-aos="fade" data-aos-delay={data.span.delay}>{data.span.text}</span>
		</div>
	</div>
	);
}
export default Keytext;